/* Page Revealer */
.revealer {
  width: 100vw;
  height: 100vh;
  position: fixed;
  z-index: 1000;
  pointer-events: none;
  display: none; }
  .revealer .logo-animation {
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: 1001; }
    .revealer .logo-animation svg {
      fill: #FFFFFF;
      display: flex;
      width: 80px;
      height: 80px; }
      .revealer .logo-animation svg #dot {
        animation: logo-pulse 2000ms linear infinite; }
  .revealer .revealer-layer {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background: #FFFFFF; }
    .revealer .revealer-layer:nth-child(2) {
      background: #AAAAAA; }
    .revealer .revealer-layer:nth-child(3) {
      background: #0c0d0e; }
  .revealer.show, .revealer.animate-in {
    display: block; }
    .revealer.show .logo-animation, .revealer.animate-in .logo-animation {
      opacity: 0;
      animation: revealer-fade-in 800ms cubic-bezier(0.215, 0.61, 0.355, 1) forwards;
      animation-delay: 1500ms; }
  .revealer.animate-in {
    display: block; }
    .revealer.animate-in .revealer-layer {
      animation: revealer-fade-in 400ms cubic-bezier(0.215, 0.61, 0.355, 1) forwards; }
      .revealer.animate-in .revealer-layer:nth-child(1), .revealer.animate-in .revealer-layer:nth-child(2) {
        display: none; }
  .revealer.animate-out {
    display: block; }
    .revealer.animate-out .logo-animation {
      animation: revealer-fade-out 400ms cubic-bezier(0.215, 0.61, 0.355, 1) forwards;
      transition-delay: 0; }
    .revealer.animate-out .revealer-layer {
      animation: revealer-fade-out 400ms cubic-bezier(0.215, 0.61, 0.355, 1) forwards; }
      .revealer.animate-out .revealer-layer:nth-child(1), .revealer.animate-out .revealer-layer:nth-child(2) {
