@media screen and (min-width: 900px) {
  .contact-section form.contact-form {
    padding-left: 3rem; } }

.contact-section form.contact-form .field {
  padding: 1rem 0;
  margin: 0;
  position: relative; }

.contact-section form.contact-form .label {
  font-size: 1rem;
  font-family: "Lato", sans-serif;
  color: #5b5d5e;
  font-weight: normal;
  margin-bottom: 0.8em; }

.contact-section form.contact-form .input, .contact-section form.contact-form .textarea {
  border-radius: 0;
  border: none;
  background: #FFFFFF;
  box-shadow: none;
  border-bottom: 1px solid #CCCCCC; }
  .contact-section form.contact-form .input::placeholder, .contact-section form.contact-form .textarea::placeholder {
    color: #8e9091; }
  .contact-section form.contact-form .input.is-danger, .contact-section form.contact-form .textarea.is-danger {
    background: #FFEFEF; }

.contact-section form.contact-form .textarea {
  border: 1px solid #CCCCCC; }

.contact-section form.contact-form .control .select {
  width: 100%; }
  .contact-section form.contact-form .control .select::after {
    border-color: #444444;
    height: 0.5em;
    width: 0.5em;
    margin-top: -0.375em;
    border-left-width: 1px;
    border-bottom-width: 1px; }
  .contact-section form.contact-form .control .select select {
    width: 100%;
    border-radius: 0;
    border: 1px solid #CCCCCC;
    box-shadow: none; }

.about-contact-section h1 {
  margin-bottom: 1.2em; }

.about-contact-section .address, .about-contact-section .contact {
  margin-bottom: 1.5em; }
  .about-contact-section .address p, .about-contact-section .contact p {
    margin-bottom: 0; }

footer h3 {
  font-size: 1rem;
  margin-bottom: 1.5rem; }

footer .columns.is-gapless > .column.address {
  padding-right: 3rem !important; }

footer .call-to-action {
  font-size: 1rem; }
  footer .call-to-action p {
    max-width: 25rem; }

footer .columns.contact {
  flex-direction: row-reverse; }
  footer .columns.contact p {
    margin-bottom: 0; }

footer .copyright {
  margin: 2em 0 0 0; }
